import React from "react";

const Navbar = () => {
	return (
		<nav className="navbar navbar-dark bg-main-color mb-5">
			<span className="navbar-brand mb-0 h1 mx-auto">Lyrics Search</span>
		</nav>
	);
};

export default Navbar;


